import React, { useState } from 'react';
import Gita from './BhagvadGita.json';
import GitaChapter from './BhagvadGitaChapters.json';

export const BhagvadGita = () => {
    const[gita,setGita]=useState(Gita);
    //const [chapterList,setChapterList]=useState(GitaChapter.sort((ch1,ch2)=>ch1.chapter_number-ch2.chapter_number));
    //setChapterList( chapterList.sort((ch1,ch2)=>ch1.chapter_number-ch2.chapter_number));
      

    const getVerse=(verse)=>{
        
        return(
        <div className='card'>
            <div className="card-header"><h4 className="card-title text-primary">{verse.text}</h4></div>
            <div className="card-body"> 
                <div className="card-text">                   
                    <div className="mb-3">                      
                        <h5 className="card-text text-success">{verse.transliteration}</h5>
                    </div>
                    <div>                      
                        <h6 className="card-text">{verse.word_meanings}</h6>
                    </div>
                </div>
            </div>
      </div> 
        )
    };

    const getChapterList=(chapter,index)=>{
        return (
          <option value={chapter.chapter_number} key={chapter.chapter_number}>{chapter.name_translation} ({chapter.chapter_number})</option>
        )
      };

    //   const onChangeChapterHandler=(e)=>{
    //     updateChapters(e.target.value);
    //   }

  return (
    <div className="container-fluid mt-3">
        <div className="card">
            <div className="card-header">
                <div>
                    <h3 className="card-title">BhagvadGita</h3>
                </div>
                {/* <div>
                    <div className="dropdown  mb-3 w-100 mt-3">                 
                        <label className="">Chapter</label>                
                         <select className="form-select" aria-labelledby="chapter" onChange={onChangeChapterHandler}>
                            {chapterList.map((chapter,index)=>getChapterList(chapter,index))}                    
                         </select>
                    </div>

                </div> */}
            </div>
           
        <div className="card-body">
        {gita.map(verse=>getVerse(verse))}
        </div>
        </div>
    </div>
  )
}
