import React, { useState } from 'react';
import BhagvadGita from './BhagvadGita.json';
import moment from 'moment';


export const HomePage = () => {

  const [gita,setGita]=useState(BhagvadGita);
  const startDate=('2022,08,04');
  const startDateMoment=moment(startDate);
  const currentDate=new Date();
  const difference=moment(currentDate).diff(startDateMoment,'days');  
  var count;
  var dayCounter=0;
  if(difference>700){
    count=difference%700;
    dayCounter=count;
  }else{
    count=difference;
    dayCounter=count+1;    
  }  
  let verse=gita.at(count);

  const today=moment(new Date()).format('LL');
  return (
    <div className='container-fluid mt-3'>
      <div className='card'>
        <div className="card-header gita-header">
          <div >
            <h3 className="card-title">Learn BhagvadGita</h3>
            <h6 className="card-title">(Lets learn a sloka daily.)</h6>
          </div>
          <div>
            <h3 className="card-title">Day {dayCounter}</h3>
            <h6 className="card-title">{today}</h6>            
          </div>
          </div>
        <div className="card-body"> 
          <div className="card-text">
            <div>                      
             <h3 className="card-text text-primary mb-3">{verse.text}</h3>
           </div>
           <div>                      
             <h5 className="card-text text-success mb-2">{verse.transliteration}</h5>
           </div>
           <div>                      
           <h6 className="card-text">{verse.word_meanings}</h6>
        </div>
        </div>
        </div>
      </div>     
    </div>
  )
}
